import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Highlighter from 'react-highlight-words';
import axios from 'axios';
import { Layout, Table, Typography, Button, Tag, Input, Icon } from 'antd';
import { contentStyle } from '../../styles';
const { Content } = Layout;
const { Title, Paragraph } = Typography;


class ReactCoinsList extends Component {

    state = {
        searchText: '',
        coinsList: [],
        loading: true
    }

    getColumnSearchProps = dataIndex => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
            <div style={{ padding: 8 }}>
            <Input
                ref={node => {
                this.searchInput = node;
                }}
                placeholder={`Search ${dataIndex}`}
                value={selectedKeys[0]}
                onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
                style={{ width: 188, marginBottom: 8, display: 'block' }}
            />
            <Button
                type="primary"
                onClick={() => this.handleSearch(selectedKeys, confirm)}
                icon="search"
                size="small"
                style={{ width: 90, marginRight: 8 }}
            >
                Search
            </Button>
            <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
                Reset
            </Button>
            </div>
        ),
        filterIcon: filtered => (
            <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
        ),
        onFilter: (value, record) =>
            record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
            if (visible) {
            setTimeout(() => this.searchInput.select());
            }
        },
        render: text => (
            <Highlighter
            highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
            searchWords={[this.state.searchText]}
            autoEscape
            textToHighlight={text.toString()}
            />
        ),
    });

    handleSearch = (selectedKeys, confirm) => {
        confirm();
        this.setState({ searchText: selectedKeys[0] });
    };
    
    handleReset = clearFilters => {
        clearFilters();
        this.setState({ searchText: '' });
    };

    componentDidMount () {
        axios.get('https://api.coingecko.com/api/v3/coins/list')
        .then(res => {
            this.setState({coinsList: res.data}, 
                () => {this.setState({loading: !this.state.loading })})
        })
        .catch(error => console.error(error))
    }

    render() {
        const { coinsList, loading } = this.state;
        const columns=[
            {
                title:'Id',
                dataIndex:'id',
                key:'id',
                ...this.getColumnSearchProps('id')
            },
            {
                title:'Symbol',
                dataIndex:'symbol',
                key:'symbol',
                ...this.getColumnSearchProps('symbol')
            },
            {
                title:'Name',
                dataIndex:'name',
                key:'name',
                ...this.getColumnSearchProps('name'),
                render: item => <Tag color="purple">{item}</Tag>
            },
            {
                title:'View Details',
                dataIndex:'id',
                key:'id',
                render: id => (<Button type="primary"><Link to={`/coins/${id}`}>View</Link></Button>)
            }
        ];
        return (
            <Layout style={{ padding: '1rem' }}>
                <Content style={contentStyle}>
                    <Title level={2}>Coins List</Title>
                    <Paragraph>This page lists cryptocurrencies available through the CoinGecko API. To view details of a given coin, click 'View' button. You can also filter by Id, Symbol or Name to drill down and find a coin.</Paragraph>
                    <Table 
                    bordered={true}
                    loading={loading} 
                    dataSource={coinsList} 
                    columns={columns}/>
                </Content>
            </Layout>
        )
    }
}

export default ReactCoinsList;
